<template>
  <div class="expansion" v-loading="loading">
      <div class="top">
      <div class="equip">
          <div class="left">设备名称</div>
          <div>
              <el-input v-model="equipName"></el-input>
          </div>
      </div>
      <div class="equip">
          <div class="left">设备图片</div>
          <div>
              <el-upload :auto-upload="false" action="#" class="upload-demo" drag>
                  <i class="el-icon-picture"></i>
                  <div class="el-upload__text">将图片拖到此处，或<em>点击上传</em></div>
              </el-upload>
          </div>
      </div>
      <div class="equip">
          <div class="left">配置文件</div>
          <div>
              <el-upload :auto-upload="false" action="#" class="upload-demo" drag>
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
              </el-upload>
          </div>
      </div>
      </div>
      <div class="bottom">
          <el-button type="primary" @click="addInform">
              添加
          </el-button>
          <el-button type="warning">
              取消
          </el-button>
      </div>
  </div>
</template>

<script>
export default {
    data(){
        return {
            equipName:'',
            loading:false
        }
    },
    methods:{
        addInform(){
            this.loading = true;
             this.$EventBus.$emit("addEquip",{title:this.equipName})
            setTimeout(()=>{
                this.loading = false;
                this.$message.success("加载参数成功！")
            },3000);
           
        }
    }
}
</script>

<style scoped>
.expansion{
    height: 100%;
}
.equip{
    width:500px;
    display: flex;
    justify-content:space-between;
    line-height: 40px;
    margin-bottom: 20px;
}
.left{
    font-weight: bold;
}
.equip>div:last-child{
    width:360px;
}
#app .el-icon-picture,
#app .el-icon-upload{
    font-size: 67px;
    color: #5394FF;
    margin: 40px 0 16px;
    line-height: 50px;
}
.bottom{
  margin: 80px 20px 0 0;
  text-align: right;
}
.bottom .el-button{
    width:120px;
}
</style>